</template>

<script>
import * as THREE from "three";
import gsap from "gsap";
import Help from "../components/Help";
import { sceneJSONData } from "../script/pvkWGJson";
import $ from 'jquery'

export default {
	name: "PreviewA",

	data() {
		return {
			elem: document.getElementById("full"),
			model: null,
			dialog: false,
			dialog1: false,
			dialog2: false,
			dialog3: false,
			sound: false,
			full_screen: false,
			clicked: false,
			hide: true,
			audio: false,
			fullscreen: false,
			items: [
				{
					src: "PVK_PhotoAlbum/IMG_2240.jpg",
				},
				{
					src: "PVK_PhotoAlbum/IMG_2243.jpg",
				},
				{
					src: "PVK_PhotoAlbum/IMG_2244.jpg",
				},
				{
					src: "WG_PhotoAlbum/IMG_2258.jpg",
				},
				{
					src: "WG_PhotoAlbum/IMG_2262_1.jpg",
				},
				{
					src: "WG_PhotoAlbum/IMG_2268_1.jpg",
				},
			],
			canvas: null,
			sceneSpacePreview: null,
			camera: null,
			scene: null,
			renderer: null,
			controls: null,
			raycaster: null,
			mouse: null,

			isUserInteracting: false,
			onPointerDownMouseX: 0,
			onPointerDownMouseY: 0,
			lon: 0,
			onPointerDownLon: 0,
			lat: 0,
			onPointerDownLat: 0,
			phi: 0,
			theta: 0,

			teleport: false,
			travel: true,

			VRSupported: false,
			VRInfo: "",
			currentSession: null,

			spheres: [],
			// panoramaImageSources: [
			//   "Pvk_WG_360_Images/pvkWG1.jpg", 
			//   "Pvk_WG_360_Images/pvkWG2.jpg", 
			//   "Pvk_WG_360_Images/pvkWG3.jpg", 
			//   "Pvk_WG_360_Images/pvkWG4.jpg", 
			//   "Pvk_WG_360_Images/pvkWG5.jpg", 
			// ],

			arrowObject: null,
			threeArrows: [],
			mesh: null,

			travelButtons: [],
			intersected: null,

			sceneJSONData: sceneJSONData
		}
	},
	components: {
		Help,
	},

	methods: {
		getFullscreenElement() {
			return (
				document.fullscreenElement || //standard property
				document.webkitFullscreenElement || //safari/opera support
				document.mozFullscreenElement || //firefox support
				document.msFullscreenElement
			); //ie/edge support
		},

		toggleFullscreen() {
			if (this.getFullscreenElement()) {
				document.exitFullscreen();
			} else {
				document.documentElement.requestFullscreen().catch(console.log);
			}
		},
		outside() {
			this.camera.position.set(0,35, 0);
		},

		init() {
			// Container
			this.sceneSpacePreview = document.getElementById("sceneSpacePreview");

			this.canvas = document.getElementById("scenePreview");

			// WebGL Renderer
			{
				this.renderer = new THREE.WebGLRenderer({
					canvas: this.canvas,
					antialias: true,
				});
				this.renderer.name = "Renderer";

				// VR
				this.renderer.xr.enabled = true;

				// this.renderer.setClearColor('#2b4541');
				this.renderer.setClearColor("#290921");
				this.renderer.setPixelRatio(window.devicePixelRatio);
				this.renderer.setSize(
					this.sceneSpacePreview.clientWidth,
					this.sceneSpacePreview.clientHeight
				);
			}

			// Scene
			{
				this.scene = new THREE.Scene();
				this.scene.name = "Scene";
			}

			// Camera
			{
				let fov = 70;
				let aspect = this.canvas.clientWidth / this.canvas.clientHeight;
				const near = 0.1;
				const far = 1000;
				this.camera = new THREE.PerspectiveCamera(fov, aspect, near, far);
				this.camera.name = "Camera";
				this.camera.target = new THREE.Vector3(0, 0, 0);
				this.camera.position.set(0,0,0);
				// this.camera.rotateY(Math.PI / 2);
				// this.camera.rotation.y += 9;
				// this.camera.up.set(0,1,0);
				// this.camera.lookAt(0,0,0);
			}

			{
				const color = 0x565656;
				const intensity = 1;
				const ambientLight = new THREE.AmbientLight(color, intensity);
				ambientLight.name = "Ambient Light";
				this.scene.add(ambientLight);
			}

			// Grid
			// {

			// 	const helper = new THREE.GridHelper( 50, 50 )
			// 	this.scene.add( helper)

			// }

			// Load Data
			{
				// let geom = new THREE.RingBufferGeometry(0.03, 0.25, 16);
				// let material = new THREE.MeshBasicMaterial({
				// 	wireframe: true,
				// 	color: 0xffff00,
				// 	opacity: 0.5,
				// 	side: THREE.DoubleSide,
				// });

				const texture = new THREE.TextureLoader().load('locationIcon.png');
				const geom = new THREE.PlaneBufferGeometry(0.3,0.3);
				const material = new THREE.MeshBasicMaterial({map:texture, side:THREE.DoubleSide, transparent:true, opacity:1});

				for (let obj of this.sceneJSONData) {
					if (obj.type === "SphereBufferGeometry") {
						let geometry = new THREE.SphereBufferGeometry(
							obj.parameters.radius,
							obj.parameters.widthSegment,
							obj.parameters.heightSegment,
							obj.parameters.phiStart
						);
						geometry.scale(-1, 1, 1);

						let texture = new THREE.TextureLoader().load(
							obj.material.panoramsSource
						);

						let mesh = new THREE.Mesh(
							geometry,
							new THREE.MeshBasicMaterial({ map: texture })
						);

						mesh.position.set(obj.position.x, obj.position.y, obj.position.z);

						for (let sphere of obj.goto) {
							let arrowButton = new THREE.Mesh(geom, material);

							arrowButton.position.set(
								sphere.position.x,
								sphere.position.y,
								sphere.position.z
							);
							arrowButton.to = sphere.to;

							arrowButton.rotation.x = sphere.rotation.x;
							arrowButton.rotation.y = sphere.rotation.y;
							arrowButton.rotation.z = sphere.rotation.z;

							mesh.add(arrowButton);
							this.travelButtons.push(arrowButton);
						}

						this.scene.add(mesh);
						this.spheres.push(mesh);
					}
				}
			}

			// Raycaster
			this.raycaster = new THREE.Raycaster();

			// Mouse
			this.mouse = new THREE.Vector2();

			this.canvas.style.touchAction = "none";
			this.canvas.addEventListener("pointerdown", this.onPointerDown, false);
			this.canvas.addEventListener("mousemove", this.onPointerOver, false);

			this.canvas.addEventListener("wheel", this.onDocumentMouseWheel, false);

			this.renderer.setAnimationLoop(this.animate);
		},

		animate() {
			this.lat = Math.max(-85, Math.min(85, this.lat));
			this.phi = THREE.MathUtils.degToRad(90 - this.lat);
			this.theta = THREE.MathUtils.degToRad(this.lon);

			this.camera.target.x = 500 * Math.sin(this.phi) * Math.cos(this.theta);
			this.camera.target.y = 500 * Math.cos(this.phi);
			this.camera.target.z = 500 * Math.sin(this.phi) * Math.sin(this.theta);

			this.camera.lookAt(this.camera.target);

			this.renderer.render(this.scene, this.camera);
		},

		updateVisitType(visitType) {
			if (visitType === "travel") {
				this.travel = true;
				this.teleport = false;
			}

			if (visitType === "teleport") {
				this.teleport = true;
				this.travel = false;
			}
		},

		onPointerOver(event) {
			// console.log('hahaha');

			this.mouse.set(
				(event.clientX / this.renderer.domElement.clientWidth) * 2 - 1,
				-(event.clientY / this.renderer.domElement.clientHeight) * 2 + 1
			);

			this.raycaster.setFromCamera(this.mouse, this.camera);
			let intersects = this.raycaster.intersectObjects(this.travelButtons);

			if (intersects.length) {
				let intersect = intersects[0];
				intersect.object.material.wireframe = false;
				this.intersected = intersect;
			} else {
				if (this.intersected) {
					this.intersected.object.material.wireframe = false;
					this.intersected = null;
				}
			}
		},

		onPointerDown(event) {
			// if (event.isPrimary === false) return;

			this.isUserInteracting = true;

			this.onPointerDownMouseX = event.clientX;
			this.onPointerDownMouseY = event.clientY;

			this.onPointerDownLon = this.lon;
			this.onPointerDownLat = this.lat;

			this.mouse.set(
				(event.clientX / this.renderer.domElement.clientWidth) * 2 - 1,
				-(event.clientY / this.renderer.domElement.clientHeight) * 2 + 1
			);

			this.raycaster.setFromCamera(this.mouse, this.camera);
			let intersects = this.raycaster.intersectObjects(this.travelButtons);

			if (intersects.length) {
				let intersect = intersects[0];
				// console.log(intersect.object.name);

				if (this.travel) {
					const tl = gsap.timeline( { } )

					tl.to( this.camera.position, {

						x: intersect.object.to.x,
						// y: intersect.object.to.y,
						z: intersect.object.to.z,
						// ease: "power2",
						ease: "power1",
						duration: 2,

						onStart:() => {

							this.travelButtons.forEach( (sphere)=>{
								sphere.visible = false;
							})

						},

						onUpdate: (tl) => {
						
							if ( tl.progress() >= 0.18 ) {
								intersect.object.parent.visible = false
							}
						},

						onUpdateParams:[tl],

						onComplete: ()=> {

							this.travelButtons.forEach((sphere)=>{
								sphere.visible = true;
							})

							intersect.object.parent.material.opacity = 1
							intersect.object.parent.visible = true

						}

					}).to( intersect.object.parent.material, {

						opacity: 0.01,
						duration: 1.4,
						ease: "power2"

					}, "-=2" )
				} else {
					this.camera.position.copy(intersect.object.to);
				}
			}

			this.canvas.addEventListener("pointermove", this.onPointerMove, false);
			this.canvas.addEventListener("pointerup", this.onPointerUp, false);
		},

		onPointerMove(event) {
			// if (event.isPrimary === false) return;

			this.lon =
				(this.onPointerDownMouseX - event.clientX) * 0.1 +
				this.onPointerDownLon;
			this.lat =
				(event.clientY - this.onPointerDownMouseY) * 0.1 +
				this.onPointerDownLat;
		},

		onPointerUp() {
			// if (event.isPrimary === false) return;

			this.isUserInteracting = false;

			this.canvas.removeEventListener("pointermove", this.onPointerMove, false);
			this.canvas.removeEventListener("pointerup", this.onPointerUp, false);
		},

		openFullscreen(id) {
			let iframe = document.getElementById(id);

			if (iframe.requestFullscreen) {
				iframe.requestFullscreen();
			} else if (iframe.mozRequestFullScreen) {
				/* Firefox */

				iframe.mozRequestFullScreen();
			} else if (iframe.webkitRequestFullscreen) {
				/* Chrome, Safari & Opera */

				iframe.webkitRequestFullscreen();
			} else if (iframe.msRequestFullscreen) {
				/* IE/Edge */

				iframe.msRequestFullscreen();
			}
		},

		onWindowResize() {
			this.renderer.setSize(
				this.sceneSpacePreview.clientWidth,
				this.sceneSpacePreview.clientHeight
			);
			this.camera.aspect =
				this.sceneSpacePreview.clientWidth /
				this.sceneSpacePreview.clientHeight;
			this.camera.updateProjectionMatrix();
		},

		onSessionStarted(session) {
			session.addEventListener("end", this.onSessionEnded);

			this.renderer.xr.setSession(session);

			this.currentSession = session;
		},

		onSessionEnded(/*event*/) {
			this.currentSession.removeEventListener("end", this.onSessionEnded);

			this.currentSession = null;
		},
	},

	mounted() {
    this.init();
    window.addEventListener("resize", this.onWindowResize, false);
    $(document).ready(function () {
      $("#button").click(function () {
        $("#slide_icon").slideToggle("slow");
      });
    });
  },
};
</script>
